	module.exports = {"data":{"markdownRemark":{"html":"<p>Lisons <strong>1 Corinthiens 14:2</strong> :</p>\n<blockquote>\n<p>« En effet, celui qui parle en langue ne parle pas aux hommes, mais à Dieu, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. »</p>\n</blockquote>\n<p>Ce verset est parfois utilisé pour expliquer que celui qui parle en langue s'adresse en réalité à Dieu et que de ce fait, <strong>il est normal que ni lui, ni les personnes présentes ne comprennent ce qu'il dit.</strong></p>\n<p>Lisons néanmoins les versets suivants <strong>1 Corinthiens 14:3-4 :</strong></p>\n<blockquote>\n<p>« Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. Celui qui parle en langue s'édifie lui-même; celui qui prophétise édifie l'Eglise. »</p>\n</blockquote>\n<p>Paul explique ici que contrairement à celui qui parle en langue, celui qui prophétise édifie l'église car ceux qui l'entendent sont en mesure de comprendre son discours et sont donc exhortés, consolés, édifiés. Nous pouvons donc comprendre ici <strong>un enseignement <u>très important</u></strong> de l'apôtre Paul : </p>\n<ol>\n<li>Il est <strong>impossible d'être édifié spirituellement</strong> par un discours ou un message <strong>si on ne le comprend pas.</strong></li>\n<li>L'édification spirituelle n'est donc <strong>pas une question de ressenti</strong> mais de <strong>compréhension intelligente</strong> de ce que Dieu nous communique.</li>\n</ol>\n<p>L'apôtre continue en déclarant que celui qui parle en langue cependant s'édifie lui-même. <strong>Cela signifie donc très clairement qu'il est en mesure de comprendre ce qu'il dit !</strong></p>\n<p>Si on pouvait être édifié par un discours qu’on ne comprend pas, pourquoi l’apôtre Paul demande-t-il de traduire pour que l'Église soit édifiée ? Si la compréhension n'était pas indispensable à l'édification, l'Église serait édifiée sans avoir besoin de comprendre quoi que ce soit de ce qui est dit \"en langue\".</p>\n<p>Le verset 5 confirme encore que la notion d'édification est indissociable de la compréhension de ce qui est dit : </p>\n<blockquote>\n<p>« Celui qui prophétise est plus grand que celui qui parle en langues, <strong>à moins que ce dernier n'interprète, pour que l'Eglise en reçoive de l'édification.</strong> »</p>\n</blockquote>\n<p>Celui qui parle en langue sans interprète est donc le seul à être édifié selon Paul, simplement parce qu'il n'y a que lui-même et Dieu qui sont en mesure de comprendre ce qu'il dit !</p>\n<p>On comprend d'ailleurs, en lisant les <strong>versets 27 et 28</strong>, que c'est bien cette idée que Paul exprime au <strong>verset 2</strong>. Il déclare alors que si personne ne comprend, celui qui parle en langue sans interprète ne peut que se parler à lui-même et à Dieu, car ses paroles sont des mystères (incompréhensibles) pour les autres.</p>\n<p>Lisons <strong>1 Corinthiens 14:2-4,27-28 :</strong></p>\n<blockquote>\n<p>« En effet, celui qui parle en langue ne parle pas aux hommes, mais à Dieu, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. Celui qui parle en langue <strong>s'édifie lui-même;</strong> celui qui prophétise édifie l'Eglise. En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; s'il n'y a point d'interprète, qu'on se taise dans l'Eglise, et <strong>qu'on parle à soi-même et à Dieu.</strong> »</p>\n</blockquote>\n<p>Paul ne dit donc pas ici que le don des langues  sert uniquement à s'adresser à Dieu, et est donc incompréhensible aux hommes, comme certains le croient. D'après ces textes, celui qui exerce le don des langues biblique est en mesure de comprendre ce qu'il dit, puisqu'il s'édifie lui-même. Les autres cependant, ne peuvent en être édifiés sans que l'on interprète, afin que ces paroles \"mystérieuses\" pour eux, leur soient compréhensibles également.</p>","frontmatter":{"title":"Mais parler en langues n'est-ce pas parler à Dieu et non aux hommes selon Paul ? Doit-on même comprendre ce que l'on dit ?"}}},"pathContext":{"slug":"/mais-parler-en-langue-n-est-ce-pas-parler-a-dieu-et-non-aux-hommes-selon-paul/"}}

